.DateRangePicker { 
    width: 100%;
}
.DateRangePickerInput__withBorder {
    border-radius: 0px;
    border: 2px solid #ba916e;
    width: 100%;
    display: flex;
}
.DateInput_input__focused {
    outline: 0;
    background: #fff;
    border: 0;
    border-top: 0;
    border-right: 0;
    border-bottom: 0;
    border-left: 0;
}
.DateInput {
    width: 100%;
}
.DateInput_input {
    font-weight: 400;
    font-size: 15px;
    line-height: 24px;
    width: 100%;
    padding: 9px 15px;
}